.calendar{display:grid;grid-template-columns:repeat(7, 1fr);align-items:center;width:100%;max-width:704px;margin:0 auto;background:var(--white-color)}.calendar .text{grid-column:1/-1;padding:2rem 1rem 0;text-align:center;color:var(--brown-deep-color)}.calendar div{grid-column:1/-1;display:grid;grid-template-columns:repeat(7, 1fr)}.calendar div.day{padding:0 1rem;margin-top:2rem}.calendar div.day span{font-weight:var(--label-weight)}.calendar div.day span:nth-child(1),.calendar div.day span:nth-child(7){color:var(--brown-color)}.calendar div.date{padding:0 1rem 3rem}.calendar div.date span{margin-top:2rem}@media screen and (max-width: 544px){.calendar div.date span{margin-top:1.5rem}}.calendar div.date span:nth-child(7n),.calendar div.date span:nth-child(7n+1){color:var(--brown-color)}.calendar div.date span.prev,.calendar div.date span.next{color:var(--foreground-color);opacity:.375}.calendar div.date span.pres.-s11{z-index:1;color:var(--white-color)}.calendar div.date span.pres.-s11::before{content:"";position:absolute;z-index:-1;display:block;top:50%;left:50%;width:2rem;height:2rem;background:var(--brown-color);border-radius:50%;transform:translate3d(-50%, -50%, 0)}.calendar span{display:block;font-size:var(--font-size);font-weight:var(--font-weight);letter-spacing:var(--font-spacing);line-height:var(--font-height);text-align:center;color:var(--brown-deep-color)}