.cell{display:flex;flex-wrap:nowrap;flex-direction:column;align-items:flex-start;width:100%;color:inherit}.cell.-box{gap:.25rem;min-height:12rem;padding:1.5rem;border:1px solid var(--border-color)}@media screen and (max-width: 768px){.cell.-box{min-height:10rem;padding:1rem}}.cell.-box:hover{border-color:rgba(var(--foreground-rgb), 1)}.cell.-box svg{position:absolute;right:1.5rem;bottom:1.5rem}.cell.-list{display:grid;grid-template-columns:repeat(2, 1fr);align-items:center;gap:.25rem 1rem;padding:1.5rem 0;border-top:1px solid var(--border-color)}@media screen and (max-width: 768px){.cell.-list{grid-template-columns:100%;padding:1rem 0}}.cell.-row{flex-direction:row;align-items:center;gap:.5rem}@media screen and (max-width: 768px){.cell.-row.-break{flex-direction:column}}.cell.-ac{align-items:center;justify-content:center}.cell.-ac *{text-align:center}.cell.-gp{gap:.5rem}.cell.-mb{margin-bottom:1rem}.cell.-hidn{overflow:hidden}